// :
import React, { useState, useEffect, Component } from 'react';
import NavigationBar from '../components/NavigationBar';
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import NextIcon from "../image/NextIcon.png";
import axios from "axios"
// 
const TextBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80vw;
  background-color: #e5e5e5;
  font-size: 18px;
  border: none;
  margin-bottom: 20px;
  padding: 10px;
  line-height: 1.2;
  white-space: pre-wrap;
`;

const TextInput = styled.input`
    width: 80px;
    height: 20px;
    margin: 10px;
`;

const Button = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 25px;
  font-size: 14px;
  color: white;
  background-color: #5b6d5b;
  :hover {
    background-color: #5b6d5b;
    opacity: 0.7;
  }
  border: none;
  border-radius: 3px;
  margin: 10px;
`;

//  (title() sub()  )
class Subject extends Component{
  render(){
    return (
      <header>
        <h1>{this.props.title}</h1>
        {this.props.sub}
      </header>
    );
  }
}

//
function ReviewStep5 () {
  const [Title, setTitle] = useState(' ');
  const [Content, setContent] = useState(' ');
  const [wordValue, setWordValue] = useState([]);

  const state = {
    contents: [
      {id: 'ReviewStep1', title: '1', desc: '', type: 1},
      {id: 'ReviewStep2', title: '2', desc: '', type: 1},
      {id: 'ReviewStep3', title: '3', desc: '', type: 1},
      {id: 'ReviewStep5', title: '4', desc: '', type: 0},
      {id: 'ReviewStep4', title: '5', desc: '', type: 1},
    ]
  }
  const handleChange = (e) => {
    setWordValue(wordValue.map(w => w.id === parseInt(e.target.name) ? {...w, value: e.target.value} : w))
  }
  const submitAnswer = () => {
    setTimeout(() => {
      axios({
        method: "put",
        url: "http://127.0.0.1:8000/api/Step5/",
        headers: { "Content-Type": "application/json" },
        params: {'s_id': sessionStorage.getItem('s_id')},
        data: { "answer": wordValue},
      }).catch(error => { alert('')
      }).then(
        alert(' !')
      );
    }, 500);
  }
  useEffect(async () => {
    const response = await axios.get(`http://127.0.0.1:8000/api/title`, {params: {'a_id': sessionStorage.getItem('a_id')}});
    setTitle(response.data['title']);
    console.log(Title);
  },[]);
    
  useEffect(async () => {
    const response = await axios.get(`http://127.0.0.1:8000/api/Step5`, {params: {'a_id': sessionStorage.getItem('a_id'), 's_id': sessionStorage.getItem('s_id')}});
    setContent(response.data['keyword'])
    setWordValue(response.data['answerlist'])
  },[]);
    return (
      <div style={{display:'flex'}}>
        <NavigationBar list={state.contents} title = {Title} prev={"Review"}/> {/*   */}
        <div style={{width: '90vw', display:'flex', flexDirection: 'column', alignItems: 'center', paddingLeft: '9vw', marginTop: '3vw'}}>
          <div style={{width: '80vw'}}>          
            <Subject title="4: " sub="    ."></Subject>
          </div>
          <div style={{display:'flex', flexDirection:'column'}}>
            <TextBox>{Content}</TextBox>
                .
            <div style={{display:'flex', width: '80vw', flexFlow:'wrap', justifyContent: 'space-between'}}>
              {wordValue.map(word => <TextInput key={word.id} placeholder={word.id+1+"."} onChange={handleChange} name={word.id}/>)}
            </div>
            <div style={{width: '80vw', display: 'flex', justifyContent: 'end'}}>
              <Button onClick={submitAnswer}></Button>
            </div>
          </div>
          <div style={{width: '80vw', display: 'flex', justifyContent: 'end'}}>
            <NavLink to="/Review/ReviewStep4">
              <img alt="" src ={NextIcon} width='37.5px' height='37.5px'/>               
              </NavLink> {/*  */}
          </div>
        </div>
    </div>
  );

}

export default ReviewStep5;
