//    ()
import React ,{Component} from 'react';
import { NavLink } from "react-router-dom";
import styled from "styled-components";
import main from '../image/main.png';
import main2 from '../image/main2.png';
import button from '../image/button.png';

//  4  
const Button = styled.button`
  width: 90%;
  height: 80%;
  color: red;
  font-size: 36px;
  border: none;
  border-radius: 20px;
`;

//4       NavLink   
class TOC extends Component{
  render(){
    var list = [];
    var i = 0;
    while (i<this.props.data.length){
      var data = this.props.data[i];
      list.push(
        <Button key={data.id} color={data.color}>
            <NavLink style={{ color: 'white', textDecoration: 'none' }} to={{pathname: `${data.id}`}}>{data.title} </NavLink>
        </Button>);
      i = i+1;
    }
    return (
      <>
        {list}
      </>
    );
  }
}

// 
class Home extends Component {
  state = {
    //TOC  
    contents: [
      {id: 'Study', title: '', desc: ' ', color: '#5b6d5b'},
      {id: 'Review', title: '', desc: ' ', color: '#94c973'},
      {id: 'Result', title: '', desc: ' ', color: '#4b754b'},
      {id: 'Login' , title: '', desc: ' ', color: '#5b6d5b'}
    ]
  }

  render() {
    return (
        /*<div style={{display: 'flex', alignItems:'center', justifyContent: 'space-around', height: '80vh'}}>
        <TOC data={this.state.contents}></TOC>
        </div>*/
        <Wrapper>
          <LeftWrapper>
              <img alt="" className='main' src={main} style={{width: '100%', height: '100%'}}/>
          </LeftWrapper>
          <RightWrapper>
			        <img alt="" className='main' src={main2} style={{width: '100%', height: '100%'}}/>
			        <div className="button" style={{position: 'absolute', top: '58%', left: '37%'}}>
                <NavLink to="/Study">
                  <img alt="" src ={button} width='60%' height='60%'/>               
                                 </NavLink>
			        </div>
          </RightWrapper>
        </Wrapper>
    )
  }
}

export default Home;


/*<Button>
            <NavLink style={{ color: 'green', textDecoration: 'none' }} to="/Study"> </NavLink>
  </Button>*/

// background-color: #37805b;
const Wrapper = styled.div`
  display: flex;
  height: 85vh;
`;

//  ( ,   )
const LeftWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  padding: 10px;
`;

//  (  ,   )
const RightWrapper = styled.div`
  padding: 10px;
  display: flex;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items : center;
  position: relative;
`;
