//  
import React, { useEffect, Component, useState } from 'react';
import { Formik } from "formik";
import styled from "styled-components";
import axios from "axios"
import {useNavigate} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
// 
function Study() {
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();
    return (
      <Formik 
          initialValues={{ title: "", content: "", email: ""}}
          onSubmit={(values, { setSubmitting }) => {
            setIsLoading(true)
            setTimeout(() => {
              axios({
                method: "post",
                url: "http://127.0.0.1:8000/api/study",
                headers: { "Content-Type": "application/json" },
                data: { "title": values['title'],"content": values['content'], "email": sessionStorage.getItem('user')},
              }).then(response => {
              sessionStorage.setItem('timer', '0')
              sessionStorage.setItem('s2', '0')
              //sessionStorage.setItem('s3', '0')
              sessionStorage.setItem('s_id', response.data['s_id'])
              sessionStorage.setItem('a_id', response.data['a_id'])
              navigate('/Study/Step1')
              })
              .catch(error => {
                alert('  ')
                setIsLoading(false)
              });
              setSubmitting(false);
            }, 500);
          }}
        >
          {props => {
            const {
              values,
              touched,
              errors,
              isSubmitting,
              handleChange,
              handleBlur,
              handleSubmit,
              submitForm
            } = props;
        return (
          isLoading ? 
          <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', marginTop: '10vh'}}>
            <div style={{display:'flex', alignItems:'center'}}>
              <CircularProgress size={30} color={'inherit'}/>
              <h2 style={{marginLeft: '10px'}}>...</h2>
            </div>
            <h4 style={{color: '#5b6d5b'}}>  :)</h4>
          </div> : 
          <form onSubmit={handleSubmit}>
            <Wrapper>
            <LeftWrapper>
            <header>
              <h2></h2>
              <h5 style={{color: "#676767"}}>      .</h5>
            </header>
              <div style={{display: 'flex', alignItems:'center'}}>
                <h4 style={{marginRight: '20px'}}> </h4>
                <input
                  name="title"
                  type="title"
                  placeholder=" " 
                  style={{width: '180px',marginBottom:'0px',padding:'5px'}} 
                  value = {values.title} 
                  onChange={handleChange}
                />
              </div>
                <ContentWrapper>
                  {` \n    ,\n  \n .`}
                </ContentWrapper>
            </LeftWrapper>
            <RightWrapper>
              <div style={{display: 'flex', flexDirection: 'column'}}>
                <h3 style={{margin: 0}}> </h3>
                <div style={{display: 'flex', alignItems:'center'}}>
                  <h5 style={{color: "#676767", marginTop: 0}}>     800 .</h5>
                  <h4 style={{marginLeft: 20, marginTop: 0}}> :</h4> 
                  <h3 style={{color: '#5b6e5b', marginTop: 0}}>{values.content.length}</h3> 
                  <h4 style={{marginTop: 0}}></h4>
                </div>
              </div>
              <BorderInput
                name="content"
                type="content"
                placeholder=" " 
                value = {values.content} 
                onChange={handleChange}
              />
              <EnterButton type="submit" disabled={isSubmitting}> </EnterButton> 
            </RightWrapper>
        </Wrapper>
      </form>
    )
    }}
    </Formik>
    
    );
};

export default Study;



//   
const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

//  ( ,   )
const LeftWrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: 85vh;
  width: 300px;
  margin-right: 10px;
  margin-Top: 30px;
`;

//  (  ,   )
const RightWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 85vh;
  width: 600px;
  margin-left: 10px;
`;

//  
const BorderInput = styled.textarea`
  height: 325px;
  width: 560px;
  padding: 20px;
  border: 1px solid #5b6d5b;
  border-radius: 20px;
  font-size: 12px;
`;

//    
const ContentWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 175px;
  width: 280px;
  font-size: 14px;
  border-radius: 20px;
  background-color: #d1dfd1;
  color: #5b6167;
  white-space: pre;
  line-height: 200%;
  margin-top: 30px;
`;

// 
const EnterButton = styled.button`
  width: 175px;
  height: 25px;
  margin-top: 20px;
  background-color: rgb(91, 109, 91);
  color: white;
  border: 1px solid rgb(91, 109, 91);
  background-color: 250ms;
  display: block;
  :hover {
    cursor: pointer;
    background-color: white;
    color: rgb(91, 109, 91);
  }
  `;
