import React, { useState } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import "../styles.css";
import user from "../image/user.png";
import axios from "axios"
import {useNavigate} from 'react-router-dom';

function Register() {
  const navigate = useNavigate();
  
  return (
    <Formik
      initialValues={{ email: "", password: "", confirmPassword: "", nickname: "", birthyear: ""}}
      onSubmit={(values, { setSubmitting }) => {
        setTimeout(() => {
          axios({
            method: "post",
            url: "http://127.0.0.1:8000/api/signup",
            headers: { "Content-Type": "application/json" },
            data: { "email": values['email'],"nickname": values['nickname'], "password": values['password'], "birthyear": values['birthyear']},
          }).then(() => navigate('/'))
          .catch(error => {
            alert(' !\n   .')
          });
          setSubmitting(false);
        }, 500);
      }}

      validationSchema={Yup.object().shape({
        email: Yup.string()
          .email("   .")
          .required("  ."),
        nickname: Yup.string()
          .required("  ."),
        password: Yup.string()
          .required("  .")
          .min(8, " 8  .")
          .matches(/(?=.*[0-9])/, "    ."),
        confirmPassword: Yup.string()
          .required("   .")
          .oneOf([Yup.ref('password'), null], "  ."),
        birthyear: Yup.number()
          .required("   .").positive().integer(),
    })}
    >

    {props => {
      const {
        values,
        touched,
        errors,
        isSubmitting,
        handleChange,
        handleBlur,
        handleSubmit
      } = props;

      const options = [];

      for (let i=2022; i >= 1900; i--) options.push(i);

      return (
        <div className="Register">
          <form className="RegisterForm" onSubmit={handleSubmit}>
            <h1 style={{textAlign: "left", marginLeft: 30}}></h1>
              <div className="container1">
                <div className="user_image">
                  <img alt="" src ={user} width='150' height='150'/>
                </div>
                <div className="register_input">
                  <table>
                    <tr>
                      <td className="label"><label htmlFor="email"></label></td>
                      <td><input
                            name="email"
                            type="text"
                            placeholder=""
                            value={values.email}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            className={errors.email && touched.email && "error"}
                          />
                          {errors.email && touched.email && (
                            <div className="input-feedback">{errors.email}</div>
                          )}
                      </td>
                    </tr>
                    <tr>
                      <td className="label"><label htmlFor="email"></label></td>
                      <td><input
                            name="password"
                            type="password"
                            placeholder=""
                            value={values.password}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            className={errors.password && touched.password && "error"}
                          />
                          {errors.password && touched.password && (
                            <div className="input-feedback">{errors.password}</div>
                          )}
                      </td>
                    </tr>
                    <tr>
                      <td className="label"><label htmlFor="email"> </label></td>
                      <td><input
                            name="confirmPassword"
                            type="password"
                            placeholder=" "
                            value={values.confirmPassword}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            className={errors.confirmPassword && touched.confirmPassword && "error"}
                          />
                          {errors.confirmPassword && touched.confirmPassword && (
                            <div className="input-feedback">{errors.confirmPassword}</div>
                          )}
                      </td>
                    </tr>
                    <tr>
                      <td className="label"><label htmlFor="email"></label></td>
                      <td><input
                            name="nickname"
                            type="nickname"
                            placeholder=""
                            value={values.nickname}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            className={errors.nickname && touched.nickname && "error"}
                          />
                          {errors.nickname && touched.nickname && (
                            <div className="input-feedback">{errors.nickname}</div>
                          )}
                      </td>
                    </tr>
                    <tr>
                      <td className="label"><label htmlFor="email"> </label></td>
                      <td><select
                            name='birthyear'
                            type='birthyear'
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.birthyear}
                            className={errors.birthyear && touched.birthyear && "error"}
                          >
                            {options.map(option => (
                              <option key={option} value={option}>{option}</option>
                            ))}
                          </select>
                          {errors.birthyear && touched.birthyear && (
                            <div className="input-feedback">{errors.birthyear}</div>
                          )}
                      </td>
                    </tr>
                  </table>
                </div>
              </div>
            <div className="Rbutton">
              <button className="registerButton" type="submit" disabled={isSubmitting}></button>  
             
            </div>
          </form>
        </div>
      );
    }}
    </Formik>
  );
};

export default Register;