// :
import React, { useState, useEffect, Component} from 'react';
import NavigationBar from '../components/NavigationBar';
import { DragBlock } from '../components/DragBlock';
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import NextIcon from "../image/NextIcon.png";
import axios from "axios"
import { Formik } from "formik";
import {useNavigate} from 'react-router-dom';

// 
const TextBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80vw;
  height: 30vh;
  background-color: #e5e5e5;
  font-size: 11px;
  border: none;
`;

//,   
const Button = styled.div`
  display: flex;
  font-weight: norwmal;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 40px;
  font-size: 14px;
  color: black;
  background-color: white;
  border: 1px solid grey;
`;

const SubmitButton = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 30px;
  font-size: 14px;
  color: white;
  background-color: #5b6d5b;
  :hover {
    background-color: #5b6d5b;
    opacity: 0.7;
  }
  border: none;
  margin-bottom: 20px;
`;

//  (title() sub()  )
class Subject extends Component{
  render(){
    return (
      <header>
        <h1>{this.props.title}</h1>
        {this.props.sub}
      </header>
    );
  }
}

//
function Step2 () {
  const [Summary, setSummary] = useState([]);
  const [Title, setTitle] = useState('');
  const [isSelected, SetSelected] = useState(false); /*,    */
  const [text, setText] = useState(""); //  
  const navigate = useNavigate();
  const [isSubmitted, SetIsSubmitted] = useState(false)
  const [isLoading, setIsLoading] = useState(false);
  const [sumview, setSumview] = useState(true);
  const [sumArray3, setSumArray3] = useState(['','', '']);
  const [type, setType] = useState(0);
  const [s1, sets1] = useState([]);
  const [s2, sets2] = useState([]);
  const [s3, sets3] = useState([]);
  const [userSummary, setUserSummary] = useState('');

  const state = {
    contents: [
      {id: 'Step1', title: '1', desc: '', type: 1},
      {id: 'Step2', title: '2', desc: '', type: 0},
      {id: 'Step3', title: '3', desc: '', type: 1},
      {id: 'Step5', title: '4', desc: '', type: 1},
      {id: 'Step4', title: '5', desc: '', type: 1},
    ]
};

  const handleChange = (e) => {
    setText(e.target.value)
    setType(1)
    console.log(text)
  }

  const handleChange3 = (e) => {
    sumArray3[parseInt(e.target.name)] = e.target.value;
    setSumArray3(sumArray3)
    setText(sumArray3)
    setType(3)
    console.log(text)
  }

  useEffect(async () => {
    const response = await axios.get(`http://127.0.0.1:8000/api/title`, {params: {'a_id': sessionStorage.getItem('a_id')}});
    setTitle(response.data['title']);
    console.log(Title);
  },[]);

  useEffect(async () => {
    const response = await axios.get(`http://127.0.0.1:8000/api/Step2`, {params: {'a_id': sessionStorage.getItem('a_id'), 's_id': sessionStorage.getItem('s_id')}});
    if(response){
      setSummary(response.data['summary']);
      console.log(response.data['s1'])
      sets1(response.data['s1'])
      sets2(response.data['s2'])
      sets3(response.data['s3'])
      SetIsSubmitted(response.data['issubmitted']);
      setUserSummary(response.data['user_summary'])
    }
  },[]);


  const handleInputChange = (e) => {
    setText(e.target.value)
  }

  let Input = null;
  /*isSelected true  ,   */
  if (isSelected) {
    if(type !== 1)
      setType(1)
    Input = <DragBlock data={Summary} setText = {setText}/>;
  } else {
    Input = <input
              defaultValue= {userSummary}
              placeholder='  .'
              style={{width: '80vw', height: '50px', marginTop: 20, backgroundColor: '#f6f6f6', borderWidth: '1px'}}
              onChange={handleChange}
              name="user_summary"
          />;
  }


  return (

    
    <Formik 
        initialValues={{user_summary: " ",}}
        onSubmit={({ setSubmitting }) => {
        setTimeout(() => {
          axios({
            method: "put",
            url: "http://127.0.0.1:8000/api/Step2/",
            headers: { "Content-Type": "application/json" },
            params: {'s_id': sessionStorage.getItem('s_id')},
            data: { "user_summary": text, "type": type},
          }).then(
            alert(' !')
          ).catch(error => { alert('')
          });
        }, 500);
      }}
      >
        {props => {
          const {
            values,
            touched,
            errors,
            isSubmitting,
            handleChange,
            handleBlur,
            handleSubmit,
            submitForm
          } = props;
  
    return (
      <form onSubmit={handleSubmit}>
        <div style={{display:'flex'}}>
          <NavigationBar list={state.contents} title={Title} prev={"Study"}/>  {/*   */}
          <div style={{width: '90vw', display:'flex', flexDirection: 'column', alignItems: 'center', paddingLeft: '9vw', marginTop: '3vw'}}>
            <div style={{width: '80vw'}}>          
              <Subject title="2: " sub={Summary.summary}></Subject>
              <div style={{width: '80vw', display:'flex'}}>
                <Button style = {{border: 'none', backgroundColor: 'white',  marginRight: 10, fontWeight: (sumview === true) ? 'bold':'normal'}} onClick={()=>{setSumview(true)}}> </Button>
                <Button style = {{border: 'none', backgroundColor: 'white', fontWeight: (sumview === false) ? 'bold':'normal'}} onClick={()=>{setSumview(false)}}> </Button>
              </div >
            </div>
            {sumview && <div style={{display:'flex', flexDirection: 'column', alignItems: 'center'}}>
              <div style={{width: '80vw', display:'flex', margin: 10}}>
                <Button onClick={()=>{ SetSelected(true); }} style = {{backgroundColor: (isSelected === true) ? '#5b6d5b':'white', color: (isSelected === true) ? 'white':'black'}}>  </Button>
                <Button onClick={()=>{ SetSelected(false); }} style = {{backgroundColor: (isSelected === false) ? '#5b6d5b':'white', color: (isSelected === false) ? 'white':'black'}}>  </Button>
              </div>
              {Input}
            </div>}
            {!sumview && <div style={{display:'flex', flexDirection: 'column', alignItems: 'center'}}>
              {' : [' + s1.join(', ') + ']'}
              <input
              placeholder='  .'
              style={{width: '80vw', height: '50px', marginTop: 20, backgroundColor: '#f6f6f6', borderWidth: '1px'}}
              onChange={handleChange3}
              name="0"
          />
          {' : [' + s2.join(', ') + ']'}
          <input
              placeholder='  .'
              style={{width: '80vw', height: '50px', marginTop: 20, backgroundColor: '#f6f6f6', borderWidth: '1px'}}
              onChange={handleChange3}
              name="1"
          />
          {' : [' + s3.join(', ') + ']'}
          <input
              placeholder='  .'
              style={{width: '80vw', height: '50px', marginTop: 20, backgroundColor: '#f6f6f6', borderWidth: '1px'}}
              onChange={handleChange3}
              name="2"
          />
          </div>}
            <div style={{width: '80vw', display: 'flex', flexDirection: 'column', alignItems: 'flex-end'}}>
              {!isSubmitted && <div><SubmitButton type="submit" disabled={isSubmitted}></SubmitButton></div>}
              <NavLink to="/Study/Step3">
                <img alt="" src ={NextIcon} width='37.5px' height='37.5px'/>               
            </NavLink> {/*  */}
            </div>
          </div>
        </div>
      </form>
    )
  }}
  </Formik>
  );
}

export default Step2;