//  
import React, { Component } from 'react';
import styled from "styled-components";
import '../App.css';

//     
const TextBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80vw-20px;
  background-color: #e5e5e5;
  border: none;
  padding: 10px;
  margin-bottom: 10px;
`;
//' '   
const AnswerBox = styled.div`
  display: flex;
  align-items: center;
  width: 80vw-10px;
  background-color: #e5e5e5;
  border: none;
  padding-right: 10px;
  margin-bottom: 10px;
`;

// 
class Answer extends Component {
  render() {
    return (
      <div style={{display:'flex', justifyContent: 'center'}}>
        <div style={{width: '80vw'}}>
          <div className='pointer' style={{marginBottom: '20px'}}> </div> {/*  */}
          <div className='pointer'> </div> {/*  */}
          <TextBox>         ,       .</TextBox>
          <TextBox>                     .</TextBox>
          <AnswerBox><div className='pointer' style={{marginRight: '20px'}}> </div>
            1.  2. (1)-(C), (2)-(D), (3)-(A), (4)-(E), (5)-(B)  3.   4. 
          </AnswerBox>
        </div>
      </div>
    );
  }
}

export default Answer;