// 
import React, { useState, useEffect, Component } from 'react';
import user from "../image/user.png";
import styled from 'styled-components';
import axios from "axios"
import {useNavigate, NavLink} from 'react-router-dom';

// ,    
const Button = styled.div`
  padding: 8px;
  font-size: 11px;
  color: white;
  background-color: #4b754b;
  :hover {
    background-color: #4b754b;
    opacity: 0.7;
  }
  border: none;
  border-radius: 5px;
  margin-left: 10px;
`;

// (div       ...     )
function MyPage () {
    const [User, setUser] = useState(' ');
    const navigate = useNavigate();
    useEffect(async () => {
      const response = await axios.get(`http://127.0.0.1:8000/api/MyPage`, {params: {'email': sessionStorage.getItem('user')}});
      setUser(response.data);
      console.log(response.data);
    },[]);

    const onRemove = () => {
      if (window.confirm(" ?")) {
        axios.delete(`http://127.0.0.1:8000/api/MyPage/`, {data: {'email': sessionStorage.getItem('user')}})
        .then(response => {
          if(response.data['delete'] === 'ok'){
            console.log(response.data)
            alert(".")
            sessionStorage.clear()
            window.location.replace("/Frontend/")
          }}).catch(error => {
            //  
          });
      }
      else {
        alert(".");
      }
    };

    return (
      <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '450px'}}>
          <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', width: '300px'}}>
            <img alt="" src ={user} width='100px' height='100px' />{/* */}
          </div>
          <div style={{width: '500px'}}>
            <h1> </h1>
            <div style={{display: 'flex', flexDirection: 'column', justifyContent: 'space-around',width: '500px', height: '300px',borderTop: '1px solid #e5e5e5'}}>
              <div style={{display: 'flex', width: '500px', alignItems: 'center'}}>
                <h4 style={{margin: '0px', width: '100px'}}></h4>
                <input readOnly value={sessionStorage.getItem('user')} style={{margin: '0px'}}/> {/* ,  */}
              </div>
              <div style={{display: 'flex', width: '500px', alignItems: 'center'}}>
                <h4 style={{margin: '0px', width: '100px'}}></h4>
                <input readOnly value={User.nickname} style={{margin: '0px'}}/> {/* ,  */}
              </div>
              <div style={{display: 'flex', width: '500px', alignItems: 'center'}}>
                <h4 style={{margin: '0px', width: '100px'}}></h4>
                <input readOnly value={User.birthyear} style={{margin: '0px'}}/> {/* ,  */}
              </div>
              <div style={{display: 'flex', width: '500px', justifyContent: 'end'}}>
                <NavLink to="/Mypage/edit"><Button> </Button></NavLink>
                <Button onClick={onRemove}> </Button>
              </div>
            </div>
          </div>
        </div>
    );
}

export default MyPage;
